import { Link } from "react-router-dom";
import numPlate from '../images/number.jpeg';
import parking from '../images/parking.jpeg';
import spd from '../images/spd.jpg';

function Category() {
 
  const cardData = [
     {
        id: 1,
        title: "Number Plate Recognition",
        cardClass : "card text-white card-category bg-secondary mb-3",
        imgUrl: numPlate,
        siteUrl:''
      },
      {
        id: 2,
        title: "Moving Vehicle Detection",
        cardClass : "card text-white card-category bg-success mb-3",
        imgUrl:parking,
        siteUrl:'http://localhost:8501/'
      },
      {
        id: 3,
        title: "Speed Violation Detection",
        cardClass : "card text-white card-category bg-primary mb-3",
        imgUrl:spd,
        siteUrl:'http://localhost:8501/'
      },
    ];


    return (
    <div className="category-comps">
      <div className="card-deck">
      {cardData.map((data) => (
      <div className= {data.cardClass}>
        <a target = '_blank' style =  {{ textDecoration: 'none', color: 'white' }} href = {data.siteUrl}>
        <img src= {data.imgUrl} className="card-img-top" alt="..."></img>
        <div className="card-body">
          <p className="card-text">
            {data.title}
          </p>
        </div>
        </a>
      </div>
      
  ))}
  </div>
    </div>
  );
}

export default Category;